.Menu {
  z-index: 2;
  position: fixed;
  top: 0;
  right: 0;
  width: 0;
  height: 102vh;
  display: flex;
  align-items: center;
  background-color: white;
  overflow: hidden;
  transition: all 1s ease-in-out;
}

.iconoCerrar {
  display: flex;
  margin-left: 40px;
  width: 50%;
  height: 80px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.bodyMenu {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.iconoCerrar:hover {
  opacity: 0.8;
}

.TextoMenu {
  font: normal normal 6.3vw IntegralCF-Bold;
  line-height: 100px;
  margin-right: 30px;
  cursor: pointer;
  display: inline-block;
}

.subrayado {
  display: inline-block;
}

.subrayado::after {
  content: '';
  width: 0px;
  height: 5px;
  display: block;
  background: black;
  transition: 300ms;
}

.subrayado:hover::after {
  width: 100%;
}

@media screen and (max-width: 5000px) {
  .TextoMenu {
    font-size: 8vw;
    line-height: 180px;
  }
}
@media screen and (max-width: 1366px) {
  .TextoMenu {
    font-size: 6.3vw;
    line-height: 100px;
  }
}
@media screen and (max-width: 1536px) {
  .TextoMenu {
    font-size: 6.3vw;
    line-height: 120px;
  }
}
@media screen and (max-width: 480px) {
  .iconoCerrar {
    margin-left: 40px;
    height: 4%;
    -webkit-tap-highlight-color: transparent;
  }
  .iconoCerrar:hover {
    height: 6%;
  }
  .bodyMenu {
    padding-top: 2vh;
  }
  .TextoMenu {
    font-size: 12vw;
    line-height: 60px;
    margin-bottom: 30px;
    margin-right: 30px;
    user-select: none;
  }
  h1 {
    -webkit-tap-highlight-color: transparent;
  }
}
