* {
  margin: 0;
  padding: 0;
}
body {
  width: 100vw;
  overflow-x: hidden;
  overflow-y: auto;
}

body::-webkit-scrollbar {
  width: 0;
  background-color: transparent;
}
body::-webkit-scrollbar-thumb {
  background-color: rgb(37, 37, 37);
  border-radius: 20%;
}
